<template>
  <div id="app" :class="`page-${$route.name}`">
    <header v-if="$route.name !== 'login'">
      <nav class="navbar navbar-expand-lg">
        <ul class="navbar-nav main">
          <li class="nav-item d-flex align-items-center">
            <router-link to="/" class="navbar-brand d-flex align-items-center">
             Cathay CRM
            </router-link>
          </li>
          <li class="nav-item d-flex align-items-center"
          :class="{
            'active': $route.name === 'index'
          }">
            <router-link to="/" class="nav-link">首頁</router-link>
          </li>
          <li class="nav-item d-flex align-items-center"
          :class="{
            'active': $route.name === 'customer-create'
          }">
            <router-link to="/customer/create" class="nav-link">新增客戶</router-link>
          </li>
          <li class="nav-item d-flex align-items-center"
          :class="{
            'active': $route.name === 'group'
          }">
            <router-link to="/group" class="nav-link">群組管理</router-link>
          </li>
          <li class="nav-item d-flex align-items-center"
          :class="{
            'active': $route.name === 'business'
          }">
            <router-link to="/business" class="nav-link">商機專區</router-link>
          </li>
          <li class="nav-item d-flex align-items-center"
          :class="{
            'active': $route.name === 'search-filter'
          }">
            <router-link to="/search-filter" class="nav-link">條件篩選</router-link>
          </li>
          <li class="nav-item d-flex align-items-center"
          :class="{
            'active': $route.name === 'insurance-check'
          }">
            <router-link to="/insurance-check" class="nav-link">保單健檢</router-link>
          </li>
          <li class="nav-item d-flex align-items-center"
          :class="{
            'active': $route.name === 'recommand'
          }">
            <router-link to="/recommand" class="nav-link">商品建議書</router-link>
          </li>
        </ul>
        <ul class="navbar-nav">
          <li class="nav-item dropdown message-dropdown">
            <a
            class="nav-link message"
            role="button"
            id="msgDropdown"
            data-toggle="dropdown"
            aria-haspopup="true"
            aria-expanded="false">
              <i class="icon-ringtone"></i>
              <div class="badget">{{messageCount}}</div>
              <div class="text">訊息</div>
            </a>
            <div class="dropdown-menu"
            aria-labelledby="msgDropdown">
              <a class="dropdown-item"
              v-show="messageMore || (!messageMore && mid < 3)"
              v-for="(msg, mid) in message"
              :key="`msg-${mid}`">
                <span class="text">{{msg.name}} {{msg.msg}}</span>
                <a @click="openUpdateDetail(mid, msg.custMsgInfo)" class="link text-underline">{{msg.link}}</a>
              </a>
              <a
              class="link text-underline collapse-item"
              v-if="!messageMore"
              @click.stop.prevent="toggleMessage(true)">
                顯示更多 <i class="icon-collapse_down"></i>
              </a>
              <a class="link text-underline collapse-item"
              v-else
              @click.stop.prevent="toggleMessage(false)">
                收合顯示 <i class="icon-collapse_up"></i>
              </a>
            </div>
          </li>
          <li class="nav-item user-info dropdown">
            <a
            class="btn btn-user"
            id="navbarDropdown"
            role="button"
            data-toggle="dropdown"
            aria-haspopup="true"
            aria-expanded="false">
              <span class="avatar">
                <i class="icon-account text-white"></i>
              </span>
              <span class="name">{{this.$store.state.userInfo.name}}</span>
              <div class="text" style="white-space:nowrap">{{this.$store.state.userInfo.workDate}}</div>
            </a>
            <div class="dropdown-menu" aria-labelledby="navbarDropdown">
              <a class="dropdown-item" href="#" @click.prevent="routeToCustDetail">個人資料</a>
              <a class="dropdown-item" href="#">後台管理</a>
              <a class="dropdown-item" href="/crm/manager.html">主管專區</a>
              <!-- <a class="dropdown-item" href="#" 
              @click="panelOpen('ToggleUserModalOpen')">切換帳號</a> -->
              <a class="dropdown-item" href="#" 
              @click="panelOpen('ToggleUserModalOpen')"
              v-if="UserTokenKey && UserInfo.apiKeyFunctionsPermission[UserTokenKey].SWITCH_USER">切換帳號</a>
              <a class="dropdown-item" href="/crm/mobile.html?size=mobile">手機版</a>
              <a v-if="!logoutLoading" class="dropdown-item text-red" @click="toSignOut()">
                登出
              </a>
              <a v-else class="dropdown-item text-red">
                登出中...
              </a>
            </div>
          </li>
        </ul>
      </nav>
    </header>
    <router-view/>

    <toggle-user-modal
    :open="ToggleUserModalOpen"
    :inputs='{}'
    @submit="checkToLogin"
    @close="panelClose('ToggleUserModalOpen')"/>

    <update-msg-modal
    :open="UpdateMsgModalOpen"
    :custMsgInfo="custMsgInfo"
    :id="selectedMessage"
    @submit="confirmUpdate"
    @close="panelClose('UpdateMsgModalOpen')" />


  </div>
</template>

<script>
import {mapActions, mapGetters} from 'vuex'
import {commonMixin} from '../mixins/common'
import UpdateMsgModal from './components/UpdateMsgModal.vue'
import ToggleUserModal from '@/desktop/components/ToggleUserModal';
import axiosFormSubmit from '../service/axiosFormSubmit.js'
import {getUserSSO} from '@/service'
import { selectMessage } from '../service'
import getCookie from '@/service/getCookie'

export default {
  name: 'App',
  mixins: [commonMixin],
  data () {
    return {
      logoutLoading:false,
      messageOpen: false,
      messageMore: false,
      selectedMessage: null,
      custMsgInfo:{},
      UpdateMsgModalOpen: false,
      ToggleUserModalOpen: false,
      messageCount: 0, 
      message: []
    };
  },
  components: { UpdateMsgModal , ToggleUserModal},
  watch: {
    '$route': 'fetchData'
  },
  computed : {
    ...mapGetters(['UserInfo','UserTokenKey','SamlStatus'])
  },
  methods: {
    ...mapActions([
      'setPageLock', 'updateUserToken' , 'updateQueryKeyword' ,'updateSamlStatus','updateCustID'
    ]),
    routeToCustDetail() {
      this.updateCustID(this.$store.state.userInfo.id)
      localStorage.cust = this.$store.state.userInfo.id
      this.$router.push({path: `/detail/cust/tab/1`})
    },
    toSignOut() {
      this.logoutLoading = true
      if(getCookie('saml-status') === 'on'){ // SAML 開啟
        let url = '/servlet/HttpDispatcher/MXAuth/logout'
        axiosFormSubmit(url,{},'使用者登出 SAML on').then(result => {
          if(result){
            const href = window.location.href // 設定動態 url
            let SAML_url = 'https://tw3.cathaylife.com.tw/pkmslogout?filename=wpslogout.html' 
            if(href.indexOf('tmycrm') !== -1){
              SAML_url = 'https://tw3.cathaylife.com.tw/pkmslogout?filename=wpslogout.html'
            }else if(href.indexOf('smycrm') !== -1){
              SAML_url = 'https://sw3.cathaylife.com.tw/pkmslogout?filename=wpslogout.html'
            }else if(href.indexOf('mycrm') !== -1){
              SAML_url = 'https://w3.cathaylife.com.tw/pkmslogout?filename=wpslogout.html'
            }
            this.initUserInfo()
            this.initcustInfo()
            this.initQueryKeyword()
            this.logoutLoading = false
            axiosFormSubmit(SAML_url,{},'SAML server 端登出')
            this.$router.push({path:'/login'}) 

          }else{
            console.log('API 異常');
            this.logoutLoading = false
            this.$router.push({path:'/login'}) 
          }
        })
      }else if(getCookie('saml-status') === 'off'){ // SAML 關閉
        let url = '/servlet/HttpDispatcher/MXAuth/logout'
        axiosFormSubmit(url,{},'使用者登出 SAML off').then(result => {
          if(result){
            this.initUserInfo()
            this.initcustInfo()
            this.initQueryKeyword()
            this.logoutLoading = false
            this.$router.push({path:'/login'}) 
          }else{
            console.log('API 異常');
            this.logoutLoading = false
            this.$router.push({path:'/login'}) 
          }
          
        })
      }else{
        console.log('SAML 狀態異常 或無狀態');
        this.logoutLoading = false
        this.$router.push({path:'/login'}) 
      }

    },
    initQueryKeyword(){
      this.updateQueryKeyword('')
    },
    initcustInfo(){
      let emptyCust = {
        lv: 0,
        familyLeader: false,
        name: '',
        gender: 0,
        birthday: '',
        mobile: '',
        phone: ['', ''],
        fax: ['', ''],
        ID: '',
        office_phone: ['', '', ''],
        address: ['', '', '', ''],
        office_address: ['', '', '', ''],
        lineid: '',
        wg_name: ''
      }
      this.$store.dispatch({
        type: 'updateCustInfo',
        data: emptyCust
      })
    },
    initUserInfo() {
      let emptyUser = {
        id: '',
        name: '',
        div_no: '',
        position: '',
        code_name: '',
        workDate: ''
      }
      this.$store.dispatch({
        type: 'updateUserInfo',
        data: emptyUser
      })
      this.updateUserToken('')
    },
    toggleMessage (bool) {
      this.messageMore = bool
      return;
    },
    openUpdateDetail (id, custMsgInfo) {

      this.selectedMessage = id
      this.custMsgInfo = custMsgInfo
      this.panelOpen('UpdateMsgModalOpen')
    },
    confirmUpdate () {
      // use selectedMessage call API to update data
      this.getMessage()
      this.panelClose('UpdateMsgModalOpen')
    },
    fetchData(val) {
      if(val.name === 'index'){
        this.getMessage()
      }
      this.setPageLock(false)
      setTimeout(() => {
        $('html, body').scrollTop(0)
        // document.body.scrollTop = 0
        // document.scrollingElement.scrollTop = 0
      }, 250)
    },
    checkToLogin(){
      let userToken = decodeURI(getCookie('api-keys'))
      if(userToken){
        this.updateSamlStatus(getCookie('saml-status'))
        getUserSSO().then(result => {
          if(result.body && result.body.userInfo){
            let user = result.body.userInfo
            user.workDate = result.body.workDate
            this.$store.dispatch({
              type: 'updateUserInfo',
              data: user
            })
            this.updateUserToken(JSON.parse(userToken)[0])
            this.$router.push({path:'/'})
          } else {
            this.$router.push({path:'/login'})
            console.log('登入失敗');  
          }
        })
      }else{
        this.$router.push({path:'/login'})
      }
    },
    getMessage() {
      this.message = []  
      selectMessage(this.$store.state.userInfo.id).then(result => {
        if(result.body) {
          let change_list = result.body.change_list || []
          change_list.forEach(ele => {
            let data  = {}
            data['name'] = ele.bef_name || ''
            data['update_at'] = ele.eft_date || ''
            
            if('ABQCNM' === ele.sub_trn_kind) {
              data['msg'] = '改名為' + ele.aft_name || '' 
              data['link'] = '編輯CRM'
            } else {
              data['msg'] = '改身份證字號'
              data['link'] = '查看'
            }
            data['custMsgInfo'] = ele
            this.message.push(data)
          })

          this.messageCount = this.message.length
        }
      })
    }
  },
  beforeMount () {
    // let userToken = decodeURI(getCookie('api-keys'))
    // if(!userToken){
    //   this.$router.push({path:'/login'})
    // }
  },
  mounted () {
    // header 縮放
    document.body.onscroll = () => {
      let scroll = window.$('html, body').scrollTop();
      let header = this.$el.querySelector('header');

      if(header) {
        if (scroll > 70) {
          header.classList.add('compact')
        } else {
          header.classList.remove('compact')
        }
      }
    }
 
  }
}
</script>
