import Vue from 'vue'
import Vuex from 'vuex'
// modules
import auth from './modules/auth'

// api
import {getNews, getCity, getShareVideo} from '../../service/index'

Vue.use(Vuex)

export default new Vuex.Store({
  modules: {
    auth
  },
  getters: {
    SliceMode: () => {
      console.info("SLICE MODE: " + ("development"=="development" && process.env.VUE_APP_AGENCY=="AJA"))
      return true
    },
    BaseUrl: () => {
      return process.env.BASE_URL
    },
    Cities: () => {
      return getCity()
    },
    News: (state) => {
      return state.news
    },
    Videos: (state) => {
      return state.videos
    },
    PageLock: (state) => {
      return state.pageLock
    },
    UserInfo: (state) => {
      return state.userInfo
    },
    UserTokenKey: (state) => {
      return state.userTokenKey
    },
    CustInfo: (state) => {
      return state.custInfo
    },
    QueryKeyword: (state) => {
      return state.queryCustKeyword
    },
    SamlStatus: (state) => {
      if(true)
        return 'off'
      else
        return state.samlStatus
    },
    FilterData: (state) => {
      return state.filterData
    },
    HasHousingLoans: (state) => {
      return state.hasHousingLoans
    },
  },
  state: {
    news: [],
    videos: [],
    pageLock: false,
    samlStatus: false,
    timeoutVal: 500,
    filterData:{},
    userInfo: {
      id: '',
      name: '',
      div_no: '',
      position: '',
      code_name: '',
      workDate: ''
    },
    hasHousingLoans:false,
    userTokenKey: '',
    custInfo: {
      lv: 0,
      familyLeader: false,
      name: '',
      gender: 0,
      birthday: '',
      mobile: '',
      phone: ['', ''],
      fax: ['', ''],
      ID: '',
      serNo: '',
      office_phone: ['', '', ''],
      address: ['', '', '', ''],
      office_address: ['', '', '', ''],
      lineid: '',
      wg_name: ''
    },
    queryCustKeyword: ''
  },
  mutations: {
    updateNews: (state, payload) => {
      state.news = payload
    },
    updateVideos: (state, payload) => {
      state.videos = payload
    },
    updatePageLock: (state, payload) => {
      state.pageLock = payload
    },
    updateUserInfo: (state, payload) => {
      state.userInfo = payload.data
    },
    updateUserToken: (state, payload) => {
      state.userTokenKey = payload
    },
    updateCustInfo: (state, payload) => {
      state.custInfo = payload
    },
    updateCustID: (state, payload) => {
      state.custInfo.ID = payload
    },
    updateSerNo: (state, payload) => {
      state.custInfo.serNo = payload
    },
    updateQueryKeyword: (state, payload) => {
      state.queryCustKeyword = payload
    },
    updateCustFamilyLeader: (state, payload) => {
      state.custInfo.familyLeader = payload
    },
    updateSamlStatus: (state, payload) => {
      state.samlStatus = payload
    },
    updateFilterData: (state, payload) => {
      state.filterData = payload
    },
    updaHousingLoans: (state, payload) => {
      state.hasHousingLoans = payload
    },
  },
  actions: {
    setPageLock ({commit}, data) {
      commit('updatePageLock', data)
    },
    updateUserInfo (context, args) {
      context.commit('updateUserInfo', args)
    },
    updateUserToken(context, args) {
      context.commit('updateUserToken', args)
    },
    updateCustInfo(context, args) {
      context.commit('updateCustInfo', args)
    },
    updateCustID(context, args) {
      context.commit('updateCustID', args)
    },
    updateSerNo(context, args) {
      context.commit('updateSerNo', args)
    },
    updateQueryKeyword(context, args) {
      context.commit('updateQueryKeyword', args)
    },
    updateCustFamilyLeader(context, args) {
      context.commit('updateCustFamilyLeader', args)
    },
    updateSamlStatus(context, args) {
      context.commit('updateSamlStatus', args)
    },
    updateFilterData(context, args) {
      context.commit('updateFilterData', args)
    },
    updaHousingLoans(context, args) {
      context.commit('updaHousingLoans', args)
    },
    async loadNews ({ commit }) {
      const data = await getNews()
      commit('updateNews', data)
    },
    async loadVideos ({commit}) {
      const data = await getShareVideo()
      if(data && data.body && data.body.dataList){
        commit('updateVideos', data.body.dataList)
      }else{
        console.log('未接收到影片資料');
      }
    }
  }
})
