    <video-modal
    :video="currentVideo"
    :open="videoModalOpen"
    @close="panelClose('videoModalOpen')" />
  </main>
</template>

<script>
import {mapActions, mapGetters} from 'vuex'
import {commonMixin} from '../../mixins/common'
import ProgressBar from 'progressbar.js'
import NewsModal from '../components/NewsModal'
import VideoModal from '../components/VideoModal'

export default {
  name: 'Index',
  mixins: [commonMixin],
  components: {
    NewsModal, VideoModal
  },
  computed: {
    ...mapGetters([
      'News', 'Videos' , 'QueryKeyword'
    ])
  },
  data () {
    return {
      currentVideo: null,
      newsModalOpen: false,
      videoModalOpen: false,
      keyowrd: '',
      // videos: [
      //   {
      //     cover: 'https://placeimg.com/160/90/any',
      //     title: '【保單健檢】係蝦米',
      //     link: 'http://aja.com.tw'
      //   },
      //   {
      //     cover: 'https://placeimg.com/160/90/any',
      //     title: '【Cathay Walker】健康，近在咫尺',
      //     link: 'http://aja.com.tw'
      //   }
      // ],
      score: {
        pNumber: {score: 999.99, unit: 'P', goal: 1800},
        docNumber: {score: 9.99, unit: '件', goal: 99},
        fyc: {score: 999.9, unit: '分', goal: 1500},
        score: 350,
        salary: 125000,
        nextGoal: 1500,
        nextSalary: 127000
      }
    }
  },
  methods: {
    ...mapActions([
      'loadNews', 'loadVideos' , 'setPageLock' , 'updateQueryKeyword'
    ]),
    onCopy () {
      this.$toast.info({title: '連結已複製'})
    },
    encodeLink (type, link) {
      let str = encodeURI(link)
      return type==='fb'
               ? `https://www.facebook.com/sharer/sharer.php?u=${str}`
               :`http://line.naver.jp/R/msg/text/?${str}`
    },
    onError (feedback) {
      console.error(feedback)
    },
    runScore () {
      var bar1 = new ProgressBar.Circle(
        document.querySelector('#progress-p'), 
        {
          strokeWidth: 6,
          easing: 'easeInOut',
          duration: 1400,
          color: '#0DABC4',
          trailColor: '#e3f8f9',
          trailWidth: 6,
          svgStyle: null,
          step: (state, circle) => {
            let value = (circle.value() * this.score.pNumber.goal).toFixed(2);
            if(value === 0){
              circle.setText('');
            }else{
              circle.setText(value + `<small class="unit">${this.score.pNumber.unit}</small>`)
            }
            // circle.setText(`${this.score.pNumber.score}<small class="unit">${this.score.pNumber.unit}</small>`)
          }
        }
      );
      bar1.animate(this.score.pNumber.score / this.score.pNumber.goal);  // Number from 0.0 to 1.0

      var bar2 = new ProgressBar.Circle(
        document.querySelector('#progress-unit'), 
        {
          strokeWidth: 6,
          easing: 'easeInOut',
          duration: 1400,
          color: '#9B62CD',
          trailColor: '#f8f2fc',
          trailWidth: 6,
          svgStyle: null,
          step: (state, circle) => {
            let value = (circle.value() * this.score.docNumber.goal).toFixed(2);
            if(value === 0){
              circle.setText('');
            }else{
              circle.setText(value + `<small class="unit">${this.score.docNumber.unit}</small>`)
            }
            // circle.setText(`${this.score.docNumber.score}<small class="unit">${this.score.docNumber.unit}</small>`)
          }
        }
      );
      bar2.animate(this.score.docNumber.score / this.score.docNumber.goal);

      var bar3 = new ProgressBar.Circle(
        document.querySelector('#progress-fyc'), 
        {
          strokeWidth: 6,
          easing: 'easeInOut',
          duration: 1400,
          color: '#74CB16',
          trailColor: '#effbe6',
          trailWidth: 6,
          svgStyle: null,
          step: (state, circle) => {
            let value = Math.round(circle.value() * this.score.fyc.goal);
            if(value === 0){
              circle.setText('');
            }else{
              circle.setText(value + `<small class="unit">${this.score.fyc.unit}</small>`)
            }
            // circle.setText(`${this.score.fyc.score}<small class="unit">${this.score.fyc.unit}</small>`)
          }
        }
      );
      bar3.animate(this.score.fyc.score / this.score.fyc.goal);

    },
    doSearch () {
      this.updateQueryKeyword(this.keyowrd)
      this.$router.push({path:'/search-result'})
    },
    openVideo(video) {
      this.currentVideo = video.link.replace(/watch.v=/,'embed/');
      this.panelOpen('videoModalOpen')
    },
    openLink(news) {
      this.currentVideo = news.link;
      this.panelOpen('videoModalOpen')
    },
  },
  mounted () {
    // F5 重新整理方案
    // if(this.$store.state.userInfo.id === ''){
    //   setTimeout(() => {
    //     this.loadNews()
    //     this.loadVideos()
    //     this.keyowrd = this.QueryKeyword //之前的搜尋紀錄留著
    //     this.runScore()
    //   }, this.$store.state.timeoutVal);
    // }else{
    //   this.loadNews()
    //   this.loadVideos()
    //   this.keyowrd = this.QueryKeyword //之前的搜尋紀錄留著
    //   // this.updateQueryKeyword('') //每到首頁就init搜尋紀錄
    //   this.runScore()
    // }

    this.loadNews()
    this.loadVideos()
    this.keyowrd = this.QueryKeyword //之前的搜尋紀錄留著
    // this.updateQueryKeyword('') //每到首頁就init搜尋紀錄
    this.runScore()

  }
}
</script>
