// 針對 F5 顧客基本資料頁 導頁到第一頁籤
const isTab1 = /detail.+tab.[2-9]/g
const ref = location.href
const tabTest = isTab1.test(ref)
if(tabTest){
  location.href = ref.replace(/tab../,'tab/1')
}

import 'bootstrap'
